// @require '../typography.scss'
// @require './style.scss'
import React from 'react';
import { render } from 'react-dom';
import { Router, Route, IndexRedirect, Redirect, hashHistory } from 'react-router';
import Home from './Home';
import Components from './Components';
import MAP from './componentInfo.tpl';


function index() {
  return (
    <Router history={hashHistory}>
      <Route path="/" component={Home}>
        <IndexRedirect to={`/components/${Object.keys(MAP)[0]}`} />
        <Route path="/components" component={Components}>
          <Route path="/components/:name" />
        </Route>
        <Route path="/" component={Components}>
          <Route path={`/components/${Object.keys(MAP)[0]}`} />
        </Route>
      </Route>
      <Redirect from="*" to={`/components/${Object.keys(MAP)[0]}`} />
    </Router>
  );
}

render(index(), document.getElementById('box'));
